@import "../../../Global/config";

.DriverDashboard {
  &__content {
    &__nav {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      &--text {
        font-family: $font-family-sub-headings;
        font-size: 1.3rem;
      }
      &--btn {
        color: $white;
        border-radius: 0.5rem;
        margin: 5px;
        border: 1px solid $primary;
        &:active,
        &:hover,
        &:focus,
        &:focus-visible {
          color: $white;
          outline: none !important;
          box-shadow: none !important;
          border: 1px solid $primary;
        }
      }
    }
  }
  &__Pagination {
    display: flex;
    justify-content: flex-end;
    &--content {
      display: flex;
      align-items: center;
      flex-direction: row;
      margin-left: 1rem;
    }
    &--next {
      margin-left: 1rem;
    }
    &--prev {
      margin-right: 1rem;
    }
  }
}

.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 27px;
  margin-left: 15px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translateX(20px);
}

.alignItemsCenter{
  align-items: center;
}

/* Rounded sliders */
.slider.round {
  border-radius: 30px;
}

.slider.round:before {
  border-radius: 50%;
}