@import "../../Global/config";

.small_text {
  font-size: 8px;
}

.Printdriver {
  display: flex;
  align-items: center;
  width: 66%;
  &__label {
    width: 29%;
  }
  &__input {
    width: 100%;
  }
  &__Checkbox {
    display: flex;
    justify-content: center;
    margin-left: 1rem;
    &--checkbx {
      margin-left: 3rem;
    }
  }
}

.ModelTitle {
  padding: 1px;
  background-color:whitesmoke;
  width: 100%;
  margin-left: 5%;
  margin-bottom: 1rem;
  width: 90%;
}

.PageTitle {
  display: flex;
  justify-content: center;
  width: 100%;
  font-family: $font-family-sans-serif;
  font-weight: 600;
  font-size: larger !important;
  margin-top:2rem;
  margin-bottom:2rem
}

.MainTitleField {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}
