.Driver-action {
  display: flex;
  flex-direction: row;
  &--pensil {
    cursor: pointer;
    margin-left: 0.7rem;
  }
  &--delete {
    margin-left: 0.7rem;
    cursor: pointer;
  }
  &--tracking_active {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(173, 216, 230, 0.3);
    padding: 0px 10px;
    border-radius: 6px;
    svg {
      stroke: rgb(58, 72, 163);
    }
  }
  &--tracking_inactive {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(245, 245, 245, 1);
    padding: 0px 10px;
    border-radius: 6px;
    svg {
      stroke: rgba(128, 128, 128, 1);
    }
  }
}
